import axios from 'axios';
import { createStore } from 'vuex'
import data from './components/data.js';
const store = createStore({
  state(){
    return {
        name : 'kim',
        age : 20,
        like : [30,40,50],
        좋아요눌렀나요: false,
        인스타데이터: data,
        more : {},
    
    }
  },
  mutations :{
    이름변경(state){
        state.name = '김'
    },
    나이변경(state, data){
        state.age +=data

    },
    좋아요변경(state,i){
        if(state.좋아요눌렀나요 == false){
            state.like[i]++
            state.좋아요눌렀나요 = true;
            console.log(state.좋아요눌렀나요)
            

        }else{
            state.like[i]--
            state.좋아요눌렀나요 = false;
            console.log(state.좋아요눌렀나요)
            
        }
     
    },
    setMore(state,data){
      state.more = data
    }
  },
  actions :{
    getData(context){
      axios.get(`https://codingapple1.github.io/vue/more0.json`).then((a)=>{
        console.log(a.data);
        context.commit('setMore',a.data)
        
      })
    },


  },


})
// 수정방법
// 1.store.js에 state 수정방법 정의
// 2.수정하고 싶으면 store.js 에 부탁

export default store