  <Container :인스타데이터="인스타데이터" :step ="step" :이미지="이미지" @write="작성한글 = $event" :선택한필터="선택한필터"/>


  <!-- <p>{{now2}} {{카운터}}</p>
  <button @click="카운터++">now버튼</button> -->

