<script>
import Container from './components/Container.vue';
 import data from './components/data.js';
 import axios from 'axios';
import { mapMutations, mapState } from 'vuex';
//  axios.post();
export default {
  name: "App",
  data(){
    return{
       인스타데이터: data,
       더보기:0,
       step : 0,
       이미지 : '',
       작성한글 : '',
       선택한필터 : '',
       카운터:0,
      
    }
  },
  mounted(){
    this.emitter.on('박스클릭함', (a)=>{
      this.선택한필터 = a
      console.log(a);
      
    })
  },

  components: {
     Container : Container,
  },
  computed : {
    // now2(){
    //   return new Date()
    // },
    // name(){
    //   return this.$store.state.name
    // },
    // age(){
    //   return this.$store.state.age
    // }
    ...mapState(['name','age','likes']),
    ...mapState({ 내이름:'name',}),

  },
   methods :{
    ...mapMutations(['setMore','좋아요']),
    now(){
      return new Date()
    },
    publish(){
      var 내게시물 = {
        name: "정우혁",
      userImage: "https://placeimg.com/100/100/arch",
      postImage: `${this.이미지}`,
      likes: 36,
      date: "May 15",
      liked: false,
      content: this.작성한글,
      filter: this.선택한필터
      };
      this.인스타데이터.unshift(내게시물);
      this.step=0;
    },
     more(){
        // axios.post('URL',{name : 'kim'}).then().catch((err)=>){
        //   err
        // }

        axios.get(`https://codingapple1.github.io/vue/more${this.더보기}.json`)
          .then( 결과 => {
           this.인스타데이터.push(결과.data);
          this.더보기++;
          
       })
    },
    upload(e){
      let 내파일 = e.target.files;
      console.log(내파일[0]);
      let url = URL.createObjectURL(내파일[0]);
      console.log(url);
      this.이미지 = url;
      this.step++;
    }
   },
};
</script>

